import React from 'react';
import { Router, Route, Link, IndexRoute, browserHistory } from 'react-router';
import App from './app';
import Home from './components/home.jsx';
import Technologies from './components/technologies';
import Products from './components/products';
import Patents from './components/patents';
import Publications from './components/publications';
import Photography from './components/photography';
import Bio from './components/bio';

const Root = () => (
  <Router history={browserHistory} >
    <Route path="/" component={Home}>
      <IndexRoute component ={Home} />
    </Route>
    <Route component={App}>
      <Route path={process.env.PUBLIC_URL + "/technologies"} component={Technologies}></Route>
      <Route path={process.env.PUBLIC_URL + "/products"} component={Products}></Route>
      <Route path={process.env.PUBLIC_URL + "/patents"} component={Patents}></Route>
      <Route path={process.env.PUBLIC_URL + "/publications"} component={Publications}></Route>
      <Route path={process.env.PUBLIC_URL + "/photography"} component={Photography}></Route>
      <Route path={process.env.PUBLIC_URL + "/bio"} component={Bio}></Route>
    </Route>
  </Router>
);

export default Root;



